import * as React from "react"
import { useSelector, useDispatch } from 'react-redux';

import {
  selectCaseArray,
 } from "../features/case/caseSlice";


import Layout from "../components/layout"
import { Seo } from "../components/seo"

function MyWorkBook() {
    const posts = useSelector(selectCaseArray);
    let uniqueTitles = [...new Set(["Корзина", ...posts.map(item => item?.theme)])];
  return <Layout>
    <section className="py-5 text-center container">
      <div className="row py-lg-5">
        <div className="col-lg-6 col-md-8 mx-auto">
          <h1 className="fw-light">Posts</h1>
          <p className="lead text-muted">{posts.length}</p>
          <div>
            {uniqueTitles.map(theme => <div key={theme}>
                <b>{theme}</b>
                <div className="d-flex flex-column mb-3">
                    {posts
                    .filter(item => item.theme === theme )
                    .map(post => <small key={post.id} className="mr-3">{post.title}</small>)}
                </div>    
                
                </div>)}
          </div>
         
        </div>
      </div>
     </section>
  </Layout>
}

export default MyWorkBook

export const Head = () => (
    <Seo />
)